/* App.css */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    color: #fff;
    padding: 10px 20px;
  }
  
  .logo {
    font-size: 24px;
  }
  
  .nav-links {
    list-style: none;
    display: flex;
    padding: 0;
  }
  
  .nav-links li {
    margin-right: 20px;
  }
  
  .nav-links a {
    text-decoration: none;
    color: #fff;
    font-weight: 500;
    transition: color 0.3s;
  }
  
  .nav-links a:hover {
    color: #ff5a5f;
  }
  
  /* App.css */
  
  
  .homepage {
    background: url('https://i.postimg.cc/qRBzRjbQ/dress-background3.jpg') no-repeat center center;
    background-size: cover;
    height: 100vh;
    display: flex;
    justify-content: center; /* Center horizontally */
    align-items: center; /* Center vertically */
    color: rgb(102, 69, 69);
    font-family: 'Dancing Script', cursive;
  }
  
  .homepage-content {
    text-align: center;
  }
  
  .homepage h1 {
    
    font-size: 4rem;
    margin-bottom: 20px;
  }
  
  .homepage p {
    font-size: 2rem;
  }
  
  
  
  
  
  /* introduction App.css */
  .introduction {
    background-color: #f7f7f7;
    padding: 50px 0;
    text-align: center;
  }
  
  
  .intro-content {
    margin: 0 auto;
    max-width: 800px;
    padding: 20px;
  }
  
  .intro-content h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  .dress-cards {
    display: flex;
    justify-content: center;
    gap: 20px;
    overflow: hidden;
  }
  
  .dress-card {
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 20px;
    transition: transform 0.2s ease;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .dress-card img {
    max-width: 100%;
  }
  
  .dress-card:hover {
    transform: scale(1.05);
    transition: transform 0.5s ease;
  }
  
  /* App.css */
  
  .products {
    background-color: #f7f7f7;
    padding: 40px 0;
  }
  
  .products h1 {
    font-size: 2.5rem;
    text-align: center;
    margin-bottom: 20px;
    color: #333;
  }
  
  .product-options,
  .product-filters {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px 0;
  }
  
  .product-options label,
  .product-filters label {
    margin-right: 10px;
  }
  
  .product-options select,
  .product-filters select {
    padding: 10px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .product-cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .product-card {
    width: 250px;
    height: 350px;
    border: 1px solid #ccc;
    margin: 10px;
    padding: 10px;
    text-align: center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    display: inline-block;
    vertical-align: top;
    overflow: hidden; /* Ensure no text overflow */
  }
  
  .product-card img {
    width: 100%; /* Occupy the entire width of the card */
    height: 85%; /* Set a fixed height for the image */
    object-fit: cover; /* Scale the image while maintaining its aspect ratio */
  }
  
  .product-card p {
    font-size: 1rem;
    margin: 5px 0;
  }
  
  /* On hover, enlarge the product card */
  .product-card:hover {
    transform: scale(1.05);
    transition: transform 0.5s ease;
  }
  
  
  
  .about {
    background: #f5f5f5;
    padding: 50px 0;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .about-content {
    text-align: center;
    max-width: 800px;
  }
  
  .about h1 {
    font-size: 36px;
    margin-bottom: 20px;
    color: #333;
  }
  
  .about p {
    font-size: 18px;
    line-height: 1.6;
    margin-bottom: 20px;
    color: #555;
  }
  
  /* Add additional styling as needed */
  
  
  
  
  
  .contact {
    background: #f5f5f5;
    padding: 50px 0;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .contact-content {
    text-align: center;
    max-width: 800px;
  }
  
  .contact h1 {
    font-size: 36px;
    margin-bottom: 20px;
    color: #333;
  }
  
  .contact p {
    font-size: 18px;
    line-height: 1.6;
    margin-bottom: 20px;
    color: #555;
  }
  
  .form-group {
    margin-bottom: 20px;
    text-align: left;
  }
  
  .form-group label {
    display: block;
    font-size: 18px;
    margin-bottom: 10px;
    color: #333;
  }
  
  .form-group input,
  .form-group textarea {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    color: #333;
  }
  
  .form-group textarea {
    height: 120px;
  }
  
  button {
    background: #007BFF;
    color: #fff;
    padding: 10px 20px;
    font-size: 18px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background: #0056b3;
  }
  
  /* Add additional styling as needed */
  
  
  
  
  
  .footer {
    background-color: #333;
    color: #fff;
    padding: 20px 0;
    text-align: center;
  }
  
  .footer-content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .builder-info {
    font-size: 18px;
  }
  
  .site-links {
    margin-top: 10px;
  }
  
  .site-links a {
    color: #fff;
    text-decoration: none;
    margin: 0 10px;
    font-size: 16px;
  }
  
  .site-links a:hover {
    text-decoration: underline;
  }
  